import React, { Component } from "react";
import { Controller } from "jsnes";
import { NES } from "jsnes";
import "./CanvasController.css";

// Mapping keyboard code to [controller, button]
const KEYS = {
  88: [1, Controller.BUTTON_A], // X
  90: [1, Controller.BUTTON_B], // Z
  17: [1, Controller.BUTTON_SELECT], // Right Ctrl
  13: [1, Controller.BUTTON_START], // Enter
  38: [1, Controller.BUTTON_UP], // Up
  40: [1, Controller.BUTTON_DOWN], // Down
  37: [1, Controller.BUTTON_LEFT], // Left
  39: [1, Controller.BUTTON_RIGHT], // Right
};

class CanvasController extends Component {
  constructor(options) {
    super();
    console.log('const canvas controller');
    this.onButtonDown = options.onButtonDown;
    this.onButtonUp = options.onButtonUp;
    this.touch = null;
    this.touch2 = null;
  }

  render() {
    return (
      <canvas
        className="CanvasController"
        onTouchStart={this.handleNewMouseDown}
        // onTouchStart={this.handleMouseDown}
        onTouchEnd={this.handleNewMouseUp}
        // onTouchEnd={this.handleMouseUp}
        // onMouseDown={this.handleMouseDown}
        // onMouseUp={this.handleMouseUp}
        ref={canvas => {
          this.canvas = canvas;
        }}
      />
    );
  }

  componentDidMount() {
    this.initCanvas();
  }

  initCanvas() {
    // this.context = this.canvas.getContext("2d");
    this.TOUCH = { UP: false, DOWN: false, LEFT: false, RIGHT: false, B: false, A: false, START: false };

    // this.imageData = this.context.getImageData(0, 0, 256, 240);
  }

  handleNewMouseDown = (e) => {
    e.preventDefault();

    let scale = 256 / parseFloat(this.canvas.style.width);
    let rect = this.canvas.getBoundingClientRect();

    console.log('e.nativeEvent.touches', e.nativeEvent.touches);

    for (let touch of e.nativeEvent.touches) {
      let x = Math.round(touch.clientX - rect.left);
      let y = Math.round(touch.clientY - rect.top);
  
      console.log('x', x, 'y', y);
  
      if (x >= 62 && x <= 114 && y >= 16 && y <= 68) {
        console.log('up');
        this.TOUCH.UP = true; 
        window.nes.buttonDown(1, Controller.BUTTON_UP);
      } else if (x >= 62 && x <= 114 && y >= 116 && y <= 166) {
        console.log('down');
        this.TOUCH.DOWN = true;      
        window.nes.buttonDown(1, Controller.BUTTON_DOWN);      
      } else if (x >= 16 && x <= 62 && y >= 66 && y <= 116) {
        console.log('left');
        this.TOUCH.LEFT = true;      
        window.nes.buttonDown(1, Controller.BUTTON_LEFT);      
      } else if (x >= 113 && x <= 160 && y >= 70 && y <= 116) {
        console.log('right');
        this.TOUCH.RIGHT = true;     
        window.nes.buttonDown(1, Controller.BUTTON_RIGHT);              
      } else if (x >= 262 && x <= 316 && y >= 60 && y <= 114) {
        console.log('b');
        this.TOUCH.B = true;   
        window.nes.buttonDown(1, Controller.BUTTON_B);         
      } else if (x >= 332 && x <= 386 && y >= 60 && y <= 114) {
        console.log('a');
        this.TOUCH.A = true;   
        window.nes.buttonDown(1, Controller.BUTTON_A);                
      } else if (x >= 176 && x <= 230 && y >= 130 && y <= 166) {
        this.TOUCH.START = true;   
        window.nes.buttonDown(1, Controller.BUTTON_START);       
        console.log('start');
      } else if (x >= 176 && x <= 230 && y >= 0 && y <= 120) {
        this.TOUCH.START = true;   
        window.nes.buttonDown(1, Controller.BUTTON_SELECT);       
        console.log('start');
      }
    }
  }

  handleMouseDown = (e) => {
    e.preventDefault();
    var touch = e.nativeEvent.touches[0];    
    this.touch = touch;  
    // if (!this.props.onMouseDown) return;

    // Make coordinates unscaled
    let scale = 256 / parseFloat(this.canvas.style.width);
    let rect = this.canvas.getBoundingClientRect();
    // let x = Math.round((e.clientX - rect.left) * scale);
    // let y = Math.round((e.clientY - rect.top) * scale);

    // let x = Math.round(e.clientX - rect.left);
    // let y = Math.round(e.clientY - rect.top);

    let x = Math.round(touch.clientX - rect.left);
    let y = Math.round(touch.clientY - rect.top);

    // console.log('this.canvas.getBoundingClientRect()', this.canvas.getBoundingClientRect());
    console.log('x', x, 'y', y);

    if (x >= 62 && x <= 114 && y >= 16 && y <= 68) {
      console.log('up');
      this.TOUCH.UP = true; 
      window.nes.buttonDown(1, Controller.BUTTON_UP);
    } else if (x >= 62 && x <= 114 && y >= 116 && y <= 166) {
      console.log('down');
      this.TOUCH.DOWN = true;      
      window.nes.buttonDown(1, Controller.BUTTON_DOWN);      
    } else if (x >= 16 && x <= 62 && y >= 66 && y <= 116) {
      console.log('left');
      this.TOUCH.LEFT = true;      
      window.nes.buttonDown(1, Controller.BUTTON_LEFT);      
    } else if (x >= 113 && x <= 160 && y >= 70 && y <= 116) {
      console.log('right');
      this.TOUCH.RIGHT = true;     
      window.nes.buttonDown(1, Controller.BUTTON_RIGHT);              
    } else if (x >= 262 && x <= 316 && y >= 60 && y <= 114) {
      console.log('b');
      this.TOUCH.B = true;   
      window.nes.buttonDown(1, Controller.BUTTON_B);         
    } else if (x >= 332 && x <= 386 && y >= 60 && y <= 114) {
      console.log('a');
      this.TOUCH.A = true;   
      window.nes.buttonDown(1, Controller.BUTTON_A);                
    } else if (x >= 176 && x <= 230 && y >= 130 && y <= 166) {
      this.TOUCH.START = true;   
      window.nes.buttonDown(1, Controller.BUTTON_START);       
      console.log('start');
    } else if (x >= 176 && x <= 230 && y >= 0 && y <= 120) {
      this.TOUCH.START = true;   
      window.nes.buttonDown(1, Controller.BUTTON_SELECT);       
      console.log('start');
    }


    if (e.nativeEvent.touches['1']) {
      var touch2 = e.nativeEvent.touches[1];   
      console.log('touch2', touch2);  

      this.touch2 = touch2;
      
  
      let scale = 256 / parseFloat(this.canvas.style.width);
      let rect = this.canvas.getBoundingClientRect();
  
      let x = Math.round(touch2.clientX - rect.left);
      let y = Math.round(touch2.clientY - rect.top);
  
      if (x >= 62 && x <= 114 && y >= 16 && y <= 68) {
        window.nes.buttonDown(1, Controller.BUTTON_UP);
      } else if (x >= 62 && x <= 114 && y >= 116 && y <= 166) {
        window.nes.buttonDown(1, Controller.BUTTON_DOWN);      
      } else if (x >= 16 && x <= 62 && y >= 66 && y <= 116) {
        window.nes.buttonDown(1, Controller.BUTTON_LEFT);      
      } else if (x >= 113 && x <= 160 && y >= 70 && y <= 116) {
        window.nes.buttonDown(1, Controller.BUTTON_RIGHT);              
      } else if (x >= 262 && x <= 316 && y >= 60 && y <= 114) {
        window.nes.buttonDown(1, Controller.BUTTON_B);         
      } else if (x >= 332 && x <= 386 && y >= 60 && y <= 114) {
        window.nes.buttonDown(1, Controller.BUTTON_A);                
      } else if (x >= 176 && x <= 230 && y >= 130 && y <= 166) {
        window.nes.buttonDown(1, Controller.BUTTON_START);       
      } else if (x >= 176 && x <= 230 && y >= 0 && y <= 120) {
        window.nes.buttonDown(1, Controller.BUTTON_SELECT);       
      }
    }




  };

  handleMouseUp = (e) => {
    this.TOUCH = { UP: false, DOWN: false, LEFT: false, RIGHT: false, B: false, A: false, START: false };
    console.log('mouse up', e)
    console.log('touch status', this.touch);
    console.log('touch2 status', this.touch2);
    window.nes.buttonUp(1, Controller.BUTTON_UP);     
    window.nes.buttonUp(1, Controller.BUTTON_DOWN);       
    window.nes.buttonUp(1, Controller.BUTTON_LEFT);       
    window.nes.buttonUp(1, Controller.BUTTON_RIGHT);   
    window.nes.buttonUp(1, Controller.BUTTON_A);       
    window.nes.buttonUp(1, Controller.BUTTON_B);           
    window.nes.buttonUp(1, Controller.BUTTON_SELECT);       
    window.nes.buttonUp(1, Controller.BUTTON_START);       
  };

  handleNewMouseUp = (e) => {
    let scale = 256 / parseFloat(this.canvas.style.width);
    let rect = this.canvas.getBoundingClientRect();
    
    for (let touch of e.changedTouches) {
      let x = Math.round(touch.clientX - rect.left);
      let y = Math.round(touch.clientY - rect.top);
  
      console.log('x', x, 'y', y);
  
      if (x >= 62 && x <= 114 && y >= 16 && y <= 68) {
        console.log('up');
        this.TOUCH.UP = true; 
        window.nes.buttonUp(1, Controller.BUTTON_UP);
      } else if (x >= 62 && x <= 114 && y >= 116 && y <= 166) {
        console.log('down');
        this.TOUCH.DOWN = true;      
        window.nes.buttonUp(1, Controller.BUTTON_DOWN);      
      } else if (x >= 16 && x <= 62 && y >= 66 && y <= 116) {
        console.log('left');
        this.TOUCH.LEFT = true;      
        window.nes.buttonUp(1, Controller.BUTTON_LEFT);      
      } else if (x >= 113 && x <= 160 && y >= 70 && y <= 116) {
        console.log('right');
        this.TOUCH.RIGHT = true;     
        window.nes.buttonUp(1, Controller.BUTTON_RIGHT);              
      } else if (x >= 262 && x <= 316 && y >= 60 && y <= 114) {
        console.log('b');
        this.TOUCH.B = true;   
        window.nes.buttonUp(1, Controller.BUTTON_B);         
      } else if (x >= 332 && x <= 386 && y >= 60 && y <= 114) {
        console.log('a');
        this.TOUCH.A = true;   
        window.nes.buttonUp(1, Controller.BUTTON_A);                
      } else if (x >= 176 && x <= 230 && y >= 130 && y <= 166) {
        this.TOUCH.START = true;   
        window.nes.buttonUp(1, Controller.BUTTON_START);       
        console.log('start');
      } else if (x >= 176 && x <= 230 && y >= 0 && y <= 120) {
        this.TOUCH.START = true;   
        window.nes.buttonUp(1, Controller.BUTTON_SELECT);       
        console.log('start');
      }
    }     
  };
}

export default CanvasController;



